import { Button, Grid, TextField, Typography } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import QRCode from "qrcode.react";
import firebase from "firebase";

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
var firebaseConfig = {
  apiKey: "AIzaSyDxU2O6XPElsLO8rsCrEuNTnlPN5HCDXBw",
  authDomain: "codesandbox-io.firebaseapp.com",
  projectId: "codesandbox-io",
  storageBucket: "codesandbox-io.appspot.com",
  messagingSenderId: "607488282557",
  appId: "1:607488282557:web:dd6d33136c11f827a7ccd4",
  measurementId: "G-S6L51HM5J6"
};
// Initialize Firebase
firebase.initializeApp(firebaseConfig);
firebase.analytics();
// firebase.initializeApp();

const QRCodeGenerator = () => {
  const [accValue, setAccValue] = useState("your acc");
  const [secValue, setSecValue] = useState("your sec");
  const [combined, setCombined] = useState("");

  useEffect(() => {
    const analytics = firebase.analytics();
    // const app = fire;
    analytics.logEvent("select_content", {
      content_type: "image",
      content_id: "P12453",
      items: [{ name: "QRCodeGenerator" }]
    });

    console.log("effect");
  }, []);

  const updateCombined = () => {
    setCombined(`autorabit://settings?akey=${accValue}&skey=${secValue}`);
  };

  const handleAccValueChange = (event: any) => {
    setAccValue(event.target.value);
    updateCombined();
  };

  const handleSecValueChange = (event: any) => {
    setSecValue(event.target.value);
    updateCombined();
  };

  const handleGenerateClick = () => {
    updateCombined();
  };

  return (
    <>
      <Typography>QR code Generator</Typography>
      <Grid container spacing={2}>
        <Grid item xs={12} md={12} lg={12}>
          <TextField
            fullWidth
            value={accValue}
            onChange={handleAccValueChange}
          ></TextField>
        </Grid>
        <Grid item xs={12} md={12} lg={12}>
          <TextField
            fullWidth
            value={secValue}
            onChange={handleSecValueChange}
          ></TextField>
        </Grid>
        <Grid item xs={12} md={12} lg={12}>
          <Button
            fullWidth
            color="primary"
            variant="contained"
            onClick={handleGenerateClick}
          >
            Generate
          </Button>
        </Grid>
        <Grid item xs={12} md={12} lg={12}>
          <QRCode value={combined} />
        </Grid>
      </Grid>
    </>
  );
};

export default QRCodeGenerator;
